	module.exports = {"data":{"prismicDocument":{"data":{"title":[{"type":"heading1","text":"Software for Home Care Services"}],"page_content":[{"type":"paragraph","text":"We know why you're here. If you work in the home care sector, we also know you're probably pushed for time. So we'll cut to the chase."},{"type":"paragraph","text":"CarePlanner is an ISO 27001 accredited, ICO compliant, all-in-one management system for domiciliary care agencies. It handles all your rota planning, invoicing, payroll, call monitoring, reporting, document storage and record-keeping needs in one secure, affordable, cloud-based system."},{"type":"paragraph","text":"If that sounds like what you need, then please go ahead and book a demo. If you want to know more, read on."},{"type":"heading2","text":"Cloud-based System"},{"type":"paragraph","text":"You can log on to CarePlanner from anywhere, using any device with a web browser and an internet connection. Care staff can use our dedicated mobile app to access rotas, view service user care details and communicate with the office, even without a constant internet connection. You can also send and receive text messages from the system."},{"type":"heading2","text":"Drag and Drop Rostering"},{"type":"paragraph","text":"Our drag-and-drop rostering system is state-of-the-art. It makes rota adjustments super simple, and yet still incorporates powerful features like run management, automated carer recommendations and real-time availability/compliance checks."},{"type":"heading2","text":"Invoicing and Payroll"},{"type":"paragraph","text":"Invoicing and payroll are fully accounted for, with the ability to enter multiple rates of charge and pay, the options to pay mileage by distance or time, and the facility to export all data to Sage Line 50 or Sage Payroll."},{"type":"heading2","text":"Document Handling"},{"type":"paragraph","text":"You can upload any document to the system – including care plans, scans of DBS checks, photos of driving licenses – and rest secure in the knowledge that it has been encrypted, duplicated and stored in top-tier facilities, ready for you to retrieve whenever you need to."},{"type":"heading2","text":"Data Retention"},{"type":"paragraph","text":"Your data always belong to you. You can export almost all information from CarePlanner whenever you need to, so if you really want to go elsewhere, there shouldn't be anything to stop you."},{"type":"heading2","text":"Testimonials"},{"type":"paragraph","text":"You shouldn't just take our word for it either - read some of our testimonials from customers who are already reaping the benefits of the system."},{"type":"paragraph","text":"There's lots to show you, and we've got a team that knows your world ready to demo the software at any time convenient to you. Book a demo here, or give us a call."}]}}},"pathContext":{"permalink":"/software-for-home-care-services","prev":{"slugs":["shiftplanning-software"],"type":"page","fields":{"permalink":"/shiftplanning-software"},"data":{"feature_name":null,"feature_tagline":null,"sell":null,"title":[{"type":"heading1","text":"Shiftplanning Software"}],"excerpt":null,"feature_image":null,"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}},"next":{"slugs":["shiftplanning-software"],"type":"page","fields":{"permalink":"/shiftplanning-software"},"data":{"feature_name":null,"feature_tagline":null,"sell":null,"title":[{"type":"heading1","text":"Shiftplanning Software"}],"excerpt":null,"feature_image":null,"key_stat_figure":null,"key_stat_text":null,"large_image":null,"summary":null,"company_name":null}}}}

